<template>
	<view class="predict">
		<view style="height: 90rpx;">
			<view class="toptitle" @click="ThreeSelect">
				<text></text>
				<text>{{predictName||'未填写'}}</text>
				<view class="changeSwitch">
					<text class="iconfont iconqiehuan" style="margin-right: 20rpx;"></text>
					<text>切换</text>
				</view>
			</view>
		</view>
		<!-- banner图 -->
		<swiper class="swiperBox" v-if="casePredictbanner.length!=0" :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
			<swiper-item  @tap='PreviewPic(index,casePredictbanner)' v-for="(item,index) in casePredictbanner" :key='index'>
				<view class="swiper-item">
					<movable-area class="movable">
						<!-- #ifdef APP-PLUS -->
						<u-lazy-load class='image' :image="item.fileDownloadUrl" img-mode='aspectFit' height='420rpx'></u-lazy-load>
						<!-- #endif -->
						<!-- #ifndef APP-PLUS -->
						<image :src='item.fileDownloadUrl' mode="aspectFit"></image>
						<!-- #endif -->
						<!-- 图片对应的名称 -->
						<movable-view direction="all" class="matchPic">
							<!-- <text class="iconTip"></text> -->
							<!-- #ifdef APP-PLUS -->
							<view class="matchTitle" v-if="stlId" @tap.stop="shareModel">
								<text class='iconfont iconzhuanfa' style="margin-right: 20rpx;"></text>
								<text>转发模型</text>
							</view>
							<!-- #endif -->
						</movable-view>
					</movable-area>
				</view>
			</swiper-item>	
		</swiper>
		<swiper class="swiperBox" v-else :indicator-dots="true" :autoplay="Imageplay" :interval="3000" :duration="1000" indicator-color="#333333" indicator-active-color="#ffffff" circular="true">
			<swiper-item>
				<view class="swiper-item">
					<movable-area class="movable">
						<image :src='defaultImg' mode="aspectFit"></image>
						<!-- 图片对应的名称 -->
						<movable-view direction="all" class="matchPic">
							<!-- <text class="iconTip"></text> -->
							<!-- #ifdef APP-PLUS -->
							<view class="matchTitle" v-if="stlId"  @tap.stop="shareModel">
								<text>转发模型</text>
							</view>
							<!-- #endif -->
						</movable-view>
					</movable-area>
				</view>
			</swiper-item>	
		</swiper>
		<!-- 术后随访数据 -->
		<view class="follow" v-for="(item,index) in predicted" :key='index'>
			<view class="title">
				<text>效果预测</text>
				<text>{{index+1}}</text>
			</view>
			<view class="content">
				<text>{{item.name||'暂无数据'}}</text>
			</view>
		</view>
		<uni-popup id="popupShare" ref="popupShare" type="share" @change="change">
			<view class="poplist">
				<view class="pop_header">
					<uni-segmented-control :current="current" :values="items" @clickItem="onClickItem" style-type='text' activeColor="#86B0D4"></uni-segmented-control>
				</view>
				<view class="content">
					 <view>
						<scroll-view scroll-y="true" class="classifyItem">
							<view class="content_item" v-for="(item,index) in dataList" :key='index'>
								<text @click="changePic(index,item.id,item)" :class="index==boolIndex?'textColor':''">{{item.name}}</text>
							</view>
						</scroll-view>
					</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import {casePredictView} from '@/utill/tools/previewImage.js'
	export default{
		data(){
			return{
				Imageplay:true,
				current:0,
				items: ['效果预测'],
				boolIndex:0,//底部选中的index
				defaultImg:uni.getStorageSync('uploadLogo')
			}
		},
		props:{
			stlId:{
				type:String,
				default:''
			},
			// 效果预测title的数据
			predicted:{
				type:Array,
				default:()=>{
					return[]
				}
			},
			casePredictbanner:{
				type:Array,
				default:()=>{
					return[]
				},
			},
			predictName:{
				type:String,
				default:'',
			},
			// 三维图形数据
			dataList:{
				type:Array,
				default:()=>{
					return[]
				},
			},
		},
		methods:{
			shareModel(){
				this.$emit('shareModel',{modelid:0})
			},
			ThreeSelect(){
				this.$refs.popupShare.open()
			},
			PreviewPic(index,data){
				casePredictView(index,data)
			},
			onClickItem(e) {
				if (this.current !== e.currentIndex) {
					this.current = e.currentIndex;
				}
			},
			change(e){},
			// 选中底部menu,切换图片与标题
			changePic(index,id,item){
				this.boolIndex=index
				console.log(id)
				console.log(item)
				if(!item.stlPictureModelIds){
					var predictData=[]
					this.bus.$emit('predictData',{predictData:predictData,stlId:item.stlModelIds,name:item.name})//改变轮播图父组件的值
					return;
				}
				var b=item.stlPictureModelIds.split(',')
				var predictData=[]
				b.map(Bitem=>{//遍历出匹配的下标
					item.previewList.filter((items)=>{
						if(items.id==Bitem){
							predictData.push(items)
						}
					})
				})
				this.bus.$emit('predictData',{predictData:predictData,stlId:item.stlModelIds,name:item.name})//改变轮播图父组件的值
			},
		},
		created() {
			console.log(this.casePredictbanner)
		}
	}
</script>

<style scoped>
	@import "@/common/css/caseCommon.css";
	.follow{
		margin-top: 20rpx;
		background-color: #FFFFFF;
		padding: 0 30rpx;
	}
	.title{
		color: #242424;
		font-size: 28rpx;
		font-weight: Bold;
		font-family: PingFang-SC-Bold;
		padding: 20rpx 0;
		border-bottom: 1px solid #F0F0F0;
	}
	.content{
		color: #000000;
		font-size: 28rpx;
		padding: 20rpx 0;
		font-family: PingFang-SC-Regular;
	}
	.pop_header /deep/ .segmented-control__item--text{
		border-bottom-width: 0;
	}
</style>
